<template><undefined>
<h1>Datatable 数据表格</h1>
<p>数据表格，用于展现大量结构化数据。</p>
<h2 id="he-shi-shi-yong"><a class="header-anchor" href="#he-shi-shi-yong" aria-hidden="true">¶</a> 何时使用</h2>
<ul>
<li>当有大量结构化的数据需要展现时；</li>
<li>当需要对数据进行排序、分页、自定义操作等复杂行为时。</li>
<li>datatable内部默认使用flex布局，当外部容器使用固定高度或flex布局时，表格头部和底部会自动固定，中间区域自动滚动。</li>
<li>bottomGap属性已被废弃，建议使用flex布局实现相同效果。</li>
</ul>
<h2 id="dai-ma-yan-shi"><a class="header-anchor" href="#dai-ma-yan-shi" aria-hidden="true">¶</a> 代码演示</h2>
<h3 id="shu-ju-datatable-json"><a class="header-anchor" href="#shu-ju-datatable-json" aria-hidden="true">¶</a> 数据-datatable.json</h3>
<pre v-pre=""><code v-pre="" class="hljs language-javascript">    {
        <span class="hljs-string">"result"</span>:[
            {
                <span class="hljs-string">"name"</span>: <span class="hljs-string">"高级动物"</span>,
                <span class="hljs-string">"time"</span>: <span class="hljs-string">"03:37"</span>,
                <span class="hljs-string">"singer"</span>: <span class="hljs-string">"窦唯"</span>,
                <span class="hljs-string">"album"</span>: <span class="hljs-string">"摇滚中国乐势力"</span>
            },
            {
                <span class="hljs-string">"name"</span>: <span class="hljs-string">"谁伴我闯荡 - (电影《Beyond日记》插曲)"</span>,
                <span class="hljs-string">"time"</span>: <span class="hljs-string">"04:13"</span>,
                <span class="hljs-string">"singer"</span>: <span class="hljs-string">"Beyond"</span>,
                <span class="hljs-string">"album"</span>: <span class="hljs-string">"25周年精选"</span>
            },
            .....
        ],
        <span class="hljs-string">"totalCount"</span>:<span class="hljs-number">11</span>,
        <span class="hljs-string">"pageSize"</span>:<span class="hljs-number">10</span>,
        <span class="hljs-string">"pageNo"</span>:<span class="hljs-number">1</span>
    }
</code></pre>
<demo-box :jsfiddle="{&quot;html&quot;:&quot;&lt;v-data-table :data=\&quot;loadData\&quot; :columns=\&quot;columns\&quot; tree-table&gt;&lt;/v-data-table&gt;\n\n\n&quot;,&quot;script&quot;:&quot;\n    import axios from 'axios'\n    export default {\n        data: function () {\n            return {\n                loadData(pramas) {\n                    return axios.get(\&quot;static/datatable.json\&quot;,pramas).then(res =&gt;{\n                        return res.data;\n                    });\n                },\n                columns:[\n                    {title:\&quot;歌名\&quot;,field:'name'},\n                    {title:\&quot;时长\&quot;,field:'time'},\n                    {title:\&quot;歌手\&quot;,field:'singer'},\n                    {title:\&quot;专辑\&quot;,field:'album'},\n                ]\n            }\n        },\n        methods:{\n            \n        }\n    }\n&quot;,&quot;style&quot;:null}">
