                  <div slot="description"><h4 id="shu-zhuang-biao-ge"><a class="header-anchor" href="#shu-zhuang-biao-ge" aria-hidden="true">¶</a> 树状表格</h4>
<p>以树形结构显示的表格，需要开启<code>tree-table</code>属性，需要时还可配置<code>treeTableOption</code>。<br>
树状表格支持客户端自动分层和服务器异步加载两种模式，默认使用的客户端自动分层模式（不支持两种模式混用），只需在返回数据中指定每行数据的id值和pid关系，组件会自动组织层级关系并进行显示。</p>
</div>
