                        <span class="hljs-keyword">return</span> res.data;
                    });
                },
                <span class="hljs-attr">columns</span>:[
                    {<span class="hljs-attr">title</span>:<span class="hljs-string">"歌名"</span>,<span class="hljs-attr">field</span>:<span class="hljs-string">'name'</span>},
                    {<span class="hljs-attr">title</span>:<span class="hljs-string">"时长"</span>,<span class="hljs-attr">field</span>:<span class="hljs-string">'time'</span>},
                    {<span class="hljs-attr">title</span>:<span class="hljs-string">"歌手"</span>,<span class="hljs-attr">field</span>:<span class="hljs-string">'singer'</span>},
                    {<span class="hljs-attr">title</span>:<span class="hljs-string">"专辑"</span>,<span class="hljs-attr">field</span>:<span class="hljs-string">'album'</span>},
                ],
                <span class="hljs-attr">treeOption</span>:{
                    <span class="hljs-attr">isAsync</span>:<span class="hljs-literal">true</span>,
