<p>选择行的事件演示；行点击的事件演示</p>
</div>
                  <div class="highlight" slot="code"><pre><code class="hljs language-html">
<span class="hljs-tag">&lt;<span class="hljs-name">v-alert</span> <span class="hljs-attr">v-if</span>=<span class="hljs-string">"checkAllMsg"</span> <span class="hljs-attr">:message</span>=<span class="hljs-string">"checkAllMsg"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">v-alert</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">v-alert</span> <span class="hljs-attr">v-if</span>=<span class="hljs-string">"clickRowMsg"</span> <span class="hljs-attr">:message</span>=<span class="hljs-string">"clickRowMsg"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">v-alert</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">v-data-table</span> <span class="hljs-attr">:data</span>=<span class="hljs-string">'loadData'</span> <span class="hljs-attr">:columns</span>=<span class="hljs-string">'columns'</span> <span class="hljs-attr">check-type</span>=<span class="hljs-string">"checkbox"</span> <span class="hljs-attr">@checkall</span>=<span class="hljs-string">"checkAll"</span> <span class="hljs-attr">@clickrow</span>=<span class="hljs-string">"clickRow"</span> <span class="hljs-attr">:current-data.sync</span>=<span class="hljs-string">'mydata'</span>&gt;</span>
      
<span class="hljs-tag">&lt;/<span class="hljs-name">v-data-table</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
    <span class="hljs-keyword">import</span> axios <span class="hljs-keyword">from</span> <span class="hljs-string">'axios'</span>
    <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
        <span class="hljs-attr">data</span>: <span class="hljs-function"><span class="hljs-keyword">function</span> (<span class="hljs-params"></span>) </span>{
            <span class="hljs-keyword">return</span> {
                loadData(pramas) {
                    <span class="hljs-keyword">return</span> axios.get(<span class="hljs-string">"static/datatable.json"</span>,pramas).then(<span class="hljs-function"><span class="hljs-params">res</span> =&gt;</span>{
                        <span class="hljs-keyword">return</span> res.data;
                    });
                },
                <span class="hljs-attr">checkAllMsg</span>:<span class="hljs-literal">null</span>,
                <span class="hljs-attr">checkRowMsg</span>:<span class="hljs-literal">null</span>,
            }
        },
        <span class="hljs-attr">methods</span>:{
            <span class="hljs-attr">checkAll</span>:<span class="hljs-function"><span class="hljs-keyword">function</span>(<span class="hljs-params">value</span>)</span>{
                <span class="hljs-keyword">this</span>.checkAllMsg = <span class="hljs-string">"当前全选状态是："</span>+value
            },
            <span class="hljs-attr">clickRow</span>:<span class="hljs-function"><span class="hljs-keyword">function</span>(<span class="hljs-params">obj</span>)</span>{
                <span class="hljs-built_in">console</span>.log(obj);
                <span class="hljs-keyword">this</span>.clickRowMsg = <span class="hljs-string">"当前点击第"</span>+obj.index+<span class="hljs-string">"行"</span>;
            }
        }
    }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div>
