                  <div slot="description"><h4 id="diao-yong-api-fang-fa"><a class="header-anchor" href="#diao-yong-api-fang-fa" aria-hidden="true">¶</a> 调用API方法</h4>
<p>可通过在datatable实例上调用API方法实现某些特定操作，如刷新数据。</p>
</div>
                  <div class="highlight" slot="code"><pre><code class="hljs language-html">
<span class="hljs-tag">&lt;<span class="hljs-name">v-data-table</span> <span class="hljs-attr">ref</span>=<span class="hljs-string">"xtable"</span> <span class="hljs-attr">:data</span>=<span class="hljs-string">'loadData'</span> <span class="hljs-attr">:columns</span>=<span class="hljs-string">'columns'</span> <span class="hljs-attr">:page-num</span>=<span class="hljs-string">"pageno"</span> <span class="hljs-attr">:current-data.sync</span>=<span class="hljs-string">'mydata'</span>&gt;</span>
      
<span class="hljs-tag">&lt;/<span class="hljs-name">v-data-table</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">br</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">v-button</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"refreshTable"</span>&gt;</span>刷新表格<span class="hljs-tag">&lt;/<span class="hljs-name">v-button</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">v-button</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"reloadTable"</span>&gt;</span>重载表格<span class="hljs-tag">&lt;/<span class="hljs-name">v-button</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">v-button</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"go2"</span>&gt;</span>跳转到第二页<span class="hljs-tag">&lt;/<span class="hljs-name">v-button</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">v-button</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"edit"</span>&gt;</span>修改当前数据<span class="hljs-tag">&lt;/<span class="hljs-name">v-button</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
    <span class="hljs-keyword">import</span> axios <span class="hljs-keyword">from</span> <span class="hljs-string">'axios'</span>
    <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
        <span class="hljs-attr">data</span>: <span class="hljs-function"><span class="hljs-keyword">function</span> (<span class="hljs-params"></span>) </span>{
            <span class="hljs-keyword">return</span> {
                loadData(pramas) {
                    <span class="hljs-keyword">return</span> axios.get(<span class="hljs-string">"static/datatable.json"</span>,pramas).then(<span class="hljs-function"><span class="hljs-params">res</span> =&gt;</span>{
                        <span class="hljs-keyword">return</span> res.data;
                    });
                },
                <span class="hljs-attr">mydata</span>: [],
                <span class="hljs-attr">pageno</span>:<span class="hljs-number">1</span>
            }
        },
        <span class="hljs-attr">methods</span>:{
