<h2 id="api"><a class="header-anchor" href="#api" aria-hidden="true">¶</a> API</h2>
<h3 id="datatable-props"><a class="header-anchor" href="#datatable-props" aria-hidden="true">¶</a> Datatable Props</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>data</td>
<td>获取表格数据的函数，初始化必需参数，返回值必须是Promise对象,该函数默认接收一个请求参数，参数构造请见data arguments</td>
<td>Function</td>
<td>-</td>
</tr>
<tr>
<td>currentData.sync</td>
<td>表格当前数据,业务开发中也可以直接修改currentData,从而重新渲染表格（仅推荐用于客户端排序、数据过滤等场景）</td>
<td>Array</td>
<td>[]</td>
</tr>
<tr>
<td>bordered</td>
<td>是否展示外边框和列边框</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>stripe</td>
<td>是否显示间隔斑马纹</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>size</td>
<td>尺寸，<code v-pre="">large</code>、<code v-pre="">small</code></td>
<td>String</td>
<td>large</td>
</tr>
<tr>
<td>columns</td>
<td>表头配置，类型为对象数据，具体请见下表：Columns Object</td>
<td>Array</td>
<td>-</td>
</tr>
<tr>
<td>pagination</td>
<td>是否启用分页</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>pageSize</td>
<td>分页大小</td>
<td>Number</td>
<td>10</td>
</tr>
<tr>
<td>pageNum</td>
<td>当前页数</td>
<td>Number</td>
<td>1</td>
</tr>
<tr>
<td>pageSizeOptions</td>
<td>分页大小可选参数</td>
<td>Array</td>
<td>[10, 20, 30, 40, 50]</td>
</tr>
<tr>
<td>checkType</td>
<td>选择框类型</td>
<td>'checkbox'或'radio'</td>
<td>-</td>
</tr>
<tr>
<td>height</td>
<td>表格高度，注意是指表格整体高度（包含表头、表格和底部分页）</td>
<td>Number</td>
<td>-</td>
</tr>
<tr>
<td>bottomGap</td>
<td>距离viewport底部的间隙距离</td>
<td>Number</td>
<td>-</td>
</tr>
<tr>
<td>responseParamsName</td>
<td>接口数据的关键字段命名，目前支持total和results,分别表示总数字段和结果字段</td>
<td>Object</td>
<td>{total:'totalCount',results: 'result'}</td>
</tr>
<tr>
<td>rowClickChecked</td>
<td>点击表格行是否选中checkbox</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>fixedLeft</td>
<td>左侧固定列数</td>
<td>Number</td>
<td>0</td>
</tr>
<tr>
<td>fixedRight</td>
<td>右侧固定列数</td>
<td>Number</td>
<td>0</td>
</tr>
<tr>
<td>treeTable</td>
<td>是否启用树状表格</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>emptyText</td>
<td>没有数据时的提示</td>
<td>String</td>
<td>老板,没有找到你想要的信息......</td>
</tr>
<tr>
<td>treeOption</td>
<td>树状表格配置</td>
<td>Object</td>
<td>{idKey: &quot;id&quot;,pidKey: &quot;pid&quot;,indent: 4,position: 0,sortKey: null, order: &quot;asc&quot;,isAsync: false,loadChildren: null}</td>
</tr>
</tbody>
</table>
<h3 id="currentdata-yu-checked-zhuang-tai-shuo-ming"><a class="header-anchor" href="#currentdata-yu-checked-zhuang-tai-shuo-ming" aria-hidden="true">¶</a> currentData与checked状态说明</h3>
<p>currentData返回当前表格数据，需要使用sync修饰符保持双向同步，在翻页、点击checkbox、点击全选按钮时会更新，其中包含每行的checked状态，具体对应字段为<code v-pre="">src_dt_checked</code>,获取方法为<code v-pre="">currentData[index]['src_dt_checked']</code>。</p>
