                    return axios.get("static/datatable.json",pramas).then(res =>{
                        return res.data;
                    });
                },
                columns:[
                    {title:"歌名",field:'name',sort:'asc'},
                    {title:"时长",field:'time'},
                    {title:"歌手",field:'singer'},
                    {title:"专辑",field:'album'},
                ],
                longColumns:[
                    {title:"歌名",field:'name',sort:'asc'},
                    {title:"时长",field:'time'},
                    {title:"歌手",field:'singer'},
                    {title:"专辑",field:'album'},
                    {title:"时长",field:'time'},
                    {title:"歌手",field:'singer'},
                    {title:"专辑",field:'album'},
                    {title:"时长",field:'time'},
                    {title:"歌手",field:'singer'},
                    {title:"专辑",field:'album'},
                    {title:"时长",field:'time'},
                    {title:"歌手",field:'singer'},
                    {title:"专辑",field:'album'},
                    {title:"操作",field:'action'},
                ],
                pageno:1,
                checkAllMsg:null,
                clickRowMsg:null,
                mydata: [],
                treeOption:{
                    isAsync:true,
